import React, {useEffect, useState} from "react"
import ListCountAndCities from './../../../../conponent/Coutn&Ð¡ities/countriesToCities'
import QuickSort from '../../../../conponent/creater-metods/quicksortMethod'


let Location=props=>{
    let co,ci
    let [listCount,SetListCount]=useState([]);
    let [listCity,SetListCity]=useState([]);

    let [selectCount,SetSelectCount]=useState(props.location.Count)
    let [selectCity,SetSelectCity]=useState(props.location.City);

    useEffect(()=>{
        SetListCount(ListCountAndCities.sort((a, b)=>{
            let x = a.Count.toLowerCase();
            let y = b.Count.toLowerCase();
            if (x < y) {return -1;}
            if (x > y) {return 1;}
            return 0;
        }))
    },[]);

     let itsReLocation=(action)=> {
         let list;
         switch (action.target.name) {
             case 'COUNT':
                           list=ListCountAndCities.filter(
                             r=>r.Count===action.currentTarget.value
                           );
                           co=action.currentTarget.value;re();
                           SetSelectCount(action.currentTarget.value);
                 return    list[0] && SetListCity(QuickSort(list[0].ListCity));

             case 'CITY':
                        ci=action.currentTarget.value;re();
                 return SetSelectCity(action.currentTarget.value)
             default :
                 return 'The City and the Count does`t exit '

         }
    };
     let re=()=>{
         let r={Count:co||selectCount||'empty',City:ci||selectCity||'empty'}
         props.itsRename("LOCATION",r)
     }
    return  <div>
                    <form>
                        <select size="0" name={'COUNT'} className={'Place-Count'} onChange={itsReLocation}>
                            <option value="empty">Count</option>
                           { listCount.map(r => <option >{r.Count}</option>) }
                        </select>
                        <select size="0" name={'CITY'} onChange={itsReLocation}>
                            <option value="empty">City</option>
                           { listCity.map(r => <option >{r}</option>) }
                        </select>


                    </form>
            </div>
}
export default Location