import React, {useState, useEffect} from 'react';
import './style/style.css';
import {connect} from 'react-redux';
import {withRouter} from 'react-router'
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";
import LoAd from "../../conponent/load";
import ProfileMenu from "./Panel/ProfileMenu";
import {getProfileAge, getProfileLocation, getProfileNickname, getProfilePhoto, getProfileSkills, getProfileStatus, getProfileTags, getGenderStatusProfile, getStatusProfile, setLoading
} from "../../user-selector/ProfileSelector";
import {GetProfileFromServer, sendInformation} from "./ProfileReducer";



let ProfileCont = (props) => {

    const [nickname, setNick] = useState(['Not Name']);
    const [photo, setPhoto] = useState('');
    const [status, setStatus] = useState('Not Status');
    const [location, setLocation] = useState({Count: 'Not Count', City: 'Not City'});
    const [age, setAge] = useState({age: 0});
    const [skills, setSkills] = useState('Not Skills');
    const [tags, setTags] = useState([]);
    const [gender, SetGender] = useState('X');
    const [double, setDouble] = useState(false);

    useEffect(() => {
        props.GetProfileFromServer(props.match.params.profile||props.state.user);
        localStorage.History='profile';
        localStorage.profile=props.match.params.profile||props.state.user;

    }, [props.match.params.profile]);

    useEffect(() => {
        if(props.Nickname) {
            setNick(props.Nickname);
            setPhoto(props.Photo);
            setStatus(props.Status);
            setLocation(props.Location);
            setAge(props.Age);
            setSkills(props.Skills);
            setTags(props.Tags);
            SetGender(props.Gender)
        }
    }, [props.Status,props.Location,props.Nickname,props.Gender,props.Tags,props.Skills,props.Age]);

    debugger
    return (
        <div className={'prof_Section'}>
           <div className={'prof_Sett'}>
                   {
                       props.state.user === (props.match.params.profile || props.state.user) &&
                        <div className={'prof_Section_user_setting'}>
                            <img className={'prof_Section_setting_img'}
                                 src={'https://imgaz.staticbg.com/customers_avatars/20170308174543_860.png'}
                                 onDoubleClick={() => setDouble(!double)}
                                 alt={'Shest'}
                            />
                            {props.state.user === (props.match.params.profile || props.state.user) &&
                             double && <ProfileMenu nickname={nickname} photo={photo} status={status} location={location}
                                                    age={age} skills={skills} gender={gender} tags={tags} sendInformation={props.sendInformation}
                                       />
                            }
                        </div>
                   }
           </div>

           <div className={'prof_Ava'}>
                <div><img className={'Ava'} src={photo} alt={'Pic'}/></div>
                <div>{status}</div>
                <div>{tags.map(r => <span> {r} </span>)}</div>
           </div>

           <div className={'prof_Desc'}>
                <div><span>{nickname.map(e=>e).join(' ')},</span><span> {age.age}</span><span> {gender}</span></div>
                <div>{skills}</div>
                <div>{location.Count}</div>
                <div>{location.City}</div>
           </div>

           <div className={'prof_Prod'}>
                  <div>Future Idea</div>
           </div>
        </div>
    )
};
const MapStateToProfileReducer = (state) => ({
     state: state.LoginReducer.user

    , Nickname: getProfileNickname(state)
    , Photo: getProfilePhoto(state)
    , Status: getProfileStatus(state)
    , Location: getProfileLocation(state)

    , Age: getProfileAge(state)
    , Skills: getProfileSkills(state)
    , Tags: getProfileTags(state)
    , Loading: setLoading(state)
    , status: getStatusProfile(state)
    , Gender: getGenderStatusProfile(state)
});

export default compose(
    connect(MapStateToProfileReducer
        , {GetProfileFromServer,sendInformation}
    )
    , withRouter
    , withAuthRedirect
)(ProfileCont)