import React from "react"

let Gender= props=> {

    let sendFormAge=(value)=>{
        debugger
        props.itsRename('GENDER',value.target.value)
    };

    return  <div>
        <form name={'gen'} onClick={sendFormAge}>

            <label>X<input name={'gen'} type="radio" value={'X'} /></label>

            <label>M<input name={'gen'} type="radio" value={'M'} /></label>

            <label>F<input name={'gen'} type="radio" value={'F'} /></label>
        </form>
            </div>
}
export default Gender