let getDay=(year,month)=>{
    //%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    //Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ Ð¼Ð°ÑÑÐ¸Ð²Ð¾Ð² Ñ Ð³Ð¾Ð´Ð°Ð¼Ð¸, Ð¸ ÑÐ²Ð½Ð¾Ðµ Ð¾Ð±Ð¾Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ, ÑÐ²Ð»ÑÐµÑÑÑ Ð»Ð¸ Ð³Ð¾Ð´ ÐºÐ¾ÑÐ¾ÑÑÐ¹ Ð²Ð½ÐµÑÐµÐ½ Ð²Ð¸ÑÐ¾ÐºÐ¾ÑÐ½ÑÐ¼
    let YearArrays=(nowYear)=> {
        let ListVis=[],ListYears=[],j=0;
        for (let i = 0; i < 100; i++) {
            ListYears[i] = 1930 + i;
            if (i % 4 === 0) {ListVis[j] = 1932 + i;j++;}
            if (nowYear===ListYears[i]) {break}
        }
        return ({ListYears,ListVis})
    };//Ð²ÐµÑÐ½ÐµÑ Ð¼Ð°ÑÑÐ¸Ð² Ñ Ð³Ð¾Ð´Ð°Ð¼Ð¸ Ð¸ c Ð²Ð¸ÑÐ¾ÐºÐ¾ÑÐ½ÑÐ¼Ð¸ Ð³Ð¾Ð´Ð°Ð¼Ð¸ 2 Ð¼Ð°ÑÑÐ¸Ð²Ð°
    //%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    let MonthsArrays=(month,year,viso)=> {
        switch (month) {
            case 'January' :return  dayF(31);
            case 'May'  :return  dayF(31);
            case 'March'  :return  dayF(31);
            case 'July' :return  dayF(31);
            case 'August'  :return  dayF(31);
            case 'October'  :return  dayF(31);
            case 'December'  :return  dayF(31);
            case 'April'  :return dayF(30);
            case 'June' :return dayF(30);
            case 'September' :return dayF(30);
            case 'November' :return dayF(30);
            case 'February'  :
                if(viso.filter(r=>r===year).length!==0){return dayF(29);}
                else {return dayF(28);}
            default : debugger
                return 'not month';
        }
    };
    //%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    let dayF=(NumbDay)=> {
        let days=[];
        for (let i = 0; i < NumbDay+1 ; i++) {
            days[i] = i+1;
            if (days[i]===NumbDay){return days}
        }};//ÑÐ¾ÑÑÐ°Ð²Ð»ÐµÐ½Ð¸Ñ Ð¼Ð°ÑÑÐ¸Ð²Ð° Ñ Ð·Ð°Ð´Ð°Ð½ÑÐ¼ ÑÐ¸ÑÐ»Ð¾Ð¼
    //%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
    let Years=YearArrays(year).ListYears;
    let Vis=YearArrays(year).ListVis;
    let Days=MonthsArrays(month,year,Vis);
    let Months=['January','February','March' ,'April','May','June','July','August', 'September','October', 'November','December'];
    return ({Years,Months,Days})
    //%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%
}
export default getDay;