import React, {useEffect, useState} from "react"
import forFormAge from '../../../../conponent/creater-metods/functionDayInMonthInYear'

let Age=props=> {
    let dat;
    debugger
    let emptyArr=[];
    let [days, SetDays]=useState(emptyArr);
    let [months, SetMonths]=useState(emptyArr);
    let [years, SetYears]=useState(emptyArr);

    let [day, SetDay]=useState(1);
    let [month, SetMonth]=useState('January');
    let [year, SetYear]=useState(2020);

    useEffect(()=>{
        debugger
        SetDays(forFormAge(year,month).Days);
        SetMonths(forFormAge(year,month).Months);
        SetYears(forFormAge(year,month).Years);
    },[month,year]);

    let itsReDate=(action)=> {
      switch (action.target.name) {
        case 'DAY': dat=action.target.value;sendFormAge();
              return SetDay(action.target.value);
        case 'MONTH':SetMonth(action.target.value);
              return sendFormAge();
        case 'YEAR':SetYear(Number(action.target.value));
              return sendFormAge();
        default    :
            return 'Date is Not'
       }
    };

    let sendFormAge=()=>{
        let dataD=new Date().getFullYear()
        dataD=dataD-year;
        let r={day:dat,month:month,year:year, age:dataD};
       console.log(r);
       props.itsRename("AGE",r)

    };

    return  <div>
        <form >
            <select onChange={itsReDate} name={'DAY'}>
                <option selected value={day}>day</option>
                {days.length!==0?days.map(i=>{return <option value={`${i}`}>{i}</option> }):''}
            </select>

            <select onChange={itsReDate} name={'MONTH'}>
                <option selected value={month}>months</option>
                {months && months.map(i=>{return <option value={`${i}`}>{i}</option> })}
            </select>

            <select onChange={itsReDate} name={'YEAR'}>
                <option selected value={year}>years</option>
                {years && years.map(i=>{return <option value={`${i}`}>{i}</option> })}
            </select>
        </form>
    </div>
}
export default Age