import React from "react"

let Photo=(props)=> {

    let reName=(action)=>{
        props.itsRename('PHOTO',action.target.value)
    };

    return  <div>
        <div>
            <img className={'Panel-Ava'} src={props.photo} alt={'rew'} />
        </div>
        <div>
            <input value={props.photo} onChange={reName} />
        </div>

            </div>
}
export default Photo
