import React, {useEffect, useState} from 'react';
import '../../../style/App.css';
import Age from "./menuItem/Age";
import Location from "./menuItem/CountAndCity";
import Nickname from "./menuItem/Nickname";
import Status from "./menuItem/Status";
import Tags from "./menuItem/Tags";
import Photo from "./menuItem/Photo";
import Skills from "./menuItem/Skills";
import Gender from "./menuItem/Gender";

let ProfileMenu = props => {
debugger
    let [nickname,SetNickname]= useState(props.nickname.map(e=>e).join(' '));
    let [photo,SetPhoto]= useState(props.photo);
    let [status,SetStatus]= useState(props.status);
    let [location,SetLocation]= useState(props.location);
    let [age,SetAge]= useState(props.age);
    let [skills,SetSkills]= useState(props.skills);
    let [gender,SetGender]= useState(props.gender);
    let [tags,SetTags]= useState(props.tags.map(e=>e).join(' '));

    useEffect(()=>{
        SetNickname(props.nickname.map(e=>e).join(' '));
        SetPhoto(props.photo);
        SetStatus(props.status);
        SetLocation(props.location);
        SetGender(props.gender);
        SetAge(props.age);
        SetSkills(props.skills);
        SetTags(props.tags.map(e=>e).join(' '));

    },[props.nickname,props.photo,props.status,props.location
        ,props.age,props.skills,props.tags,props.gender
    ]);

    let itsRename=(name,value)=> {
        switch (name) {
            case "NICKNAME" :
                return SetNickname(value);
            case "PHOTO" :
                return SetPhoto(value);
            case "STATUS" :
                return SetStatus(value);
            case "LOCATION" :
                return SetLocation(value);
            case "AGE" :
                return SetAge(value);
            case "SKILLS" :
                return SetSkills(value);
            case "TAGS" :
                return SetTags(value);
            case "GENDER" :
                return SetGender(value);
            default      :
                return 'list null'
        }
    };
    let SubmitS=()=>{
        let info={nickname:nickname.match(/(\w+)/gim),photo,status,location,age,gender,skills,tags:tags.match(/(\w+)/gim)||[]};
        props.sendInformation(info)
    };
    return <div className={'prof_Section_setting_panel'}>
                     Nickname   <Nickname nickname={nickname} itsRename={itsRename}/>
                     Status     <Status status={status} itsRename={itsRename}/>
                     Gender     <Gender itsRename={itsRename}/>
                     Location   <Location location={location} itsRename={itsRename} />
                     Birthday   <Age itsRename={itsRename} />
                     Skills     <Skills skills={skills} itsRename={itsRename}/>
                     Photo      <Photo itsRename={itsRename} photo={photo} />
                     Tags       <Tags tags={tags} itsRename={itsRename}/>

                     <button onClick={()=>SubmitS()}>Save Information</button>
           </div>

};
export default ProfileMenu
